<template>
    <div class="writer-wrapper">
        <div class="btn-groups">
            <a-button @click="backToList" type="primary" shape="round">
                返回文章列表
            </a-button>
            <a-button @click="saveArticle" type="primary" style="margin-left:16px" shape="round">
                保存
            </a-button>
        </div>
        <div class="field">
            <div class="title">标题</div>
            <a-input v-model:value="title" placeholder="请输入标题"></a-input>
        </div>
        <div class="field">
            <v-md-editor v-model="content" style="min-height:400px" @change="mdChange" @save="saveArticle"></v-md-editor>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            title: '',
            content: '', // 输入的markdown
            html: '', // 及时转的html
        }
    },
    methods: {
        mdChange(text, html) {
            this.html = html
        },
        saveArticle() {
            let params = {
                id: null,
                title: this.title,
                content: this.content,
                html: this.html,
                author: this.$store.state.userInfo.nickname,
            }
            console.log(params)
            if (this.$store.state.curWritingArticleId) {
                params.id = this.$store.state.curWritingArticleId
                this.updateArticle(params)
            } else {
                this.addArticle(params)
            }
        },
        // 根据id获取文章
        getArticle() {
            this.$api.article
                .getArticle({ id: this.$store.state.curWritingArticleId })
                .then((res) => {
                    this.title = res.data.title
                    this.content = res.data.content
                    this.html = res.data.html
                    this.$store.commit('setCurWritingArticleId', res.data.id)
                })
        },
        // 更新文章
        updateArticle(params) {
            this.$api.article.updateArticle(params).then(() => {
                this.$message.success('更新成功')
            })
        },
        // 新增文章
        addArticle(params) {
            this.$api.article.addArticle(params).then((res) => {
                this.$store.commit('setCurWritingArticleId', res.data)
                this.$message.success('新增成功')
                this.getArticle()
            })
        },
        // 将图片上传到服务器，返回地址替换到md中
        uploadImage(pos, $file) {
            const formData = new FormData()
            formData.append('file', $file)
            this.$api.common.uploadImage(formData).then((res) => {
                this.$refs.md.$img2Url(pos, res.data)
            })
        },
        backToList() {
            this.$router.push({ path: '/home' })
            this.$store.commit('setCurWritingArticleId', null)
        },
    },
    mounted() {
        if (this.$store.state.curWritingArticleId) {
            this.getArticle()
        }
    },
}
</script>

<style lang="scss">
.writer-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .btn-groups {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 20px;
        align-items: center;
    }

    .field {
        width: 100%;
        margin-bottom: 16px;
        display: flex;
        align-items: center;

        .title {
            width: 100px;
            margin-right: 16px;
        }

        .markdown-body {
            height: 600px;

            .content-input-wrapper {
                height: 100%;

                textarea {
                    overflow: auto !important;
                }
            }
        }
    }
}
</style>
